.contacts-info {
  width: 280px;
  margin: 0;
  margin-bottom: 20px;
  padding: 0;
  border: none;
  border-bottom: 1px solid #e5e5e5;
}

.contacts-info__title {
  margin: 0;
  margin-bottom: 20px;
  font-weight: 700;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
}

.contacts-info__list {
  margin: 0;
  margin-bottom: 10px;
  padding: 0;
  list-style: none;
}

.contacts-info__block {
  position: relative;
  margin: 0;
  padding: 0;

  &::after {
    content: "";
    position: absolute;
    right: 0;
    top: 36px;
    width: 58px;
    height: 58px;
    background-color: #e6e6e6;
    background-position: 50% 50%;
    background-repeat: no-repeat;
  }

  &--phone::after {
    background-image: url("../../img/icon-phone.svg");
  }

  &--email::after {
    background-image: url("../../img/icon-mail.svg");
  }
}

.contacts-info__label {
  margin-bottom: 10px;
  margin-left: 20px;
  font-size: 14px;
  line-height: 26px;
  text-transform: uppercase;
  color: #333333;
}

.contacts-info__input {
  box-sizing: border-box;
  width: 100%;
  height: 58px;
  margin-top: 10px;
  margin-bottom: 15px;
  padding-left: 20px;
  padding-right: 20px;
  border: 4px solid #f2f2f2;
  background-color: #f2f2f2;

  &:hover {
    background-color: #e5e5e5;
    border-color: #e5e5e5;
  }

  &:active,
  &:focus {
    background-color: #ffffff;
    border: 4px solid #e5e5e5;
    outline: none;
  }
}

@media (min-width: @tablet-width) {
  .contacts-info {
    width: 650px;
    margin-top: 40px;
    border: none;
  }

  .contacts-info__title {
    display: flex;
    align-items: center;
    font-size: 21px;

    &::before,
    &::after {
      content: "";
      flex-grow: 1;
      flex-shrink: 0;
      width: 125px;
      height: 1px;
      background-color: #e5e5e5;
    }

    &::before {
      margin-right: 60px;
    }

    &::after {
      margin-left: 60px;
    }
  }
}

@media (min-width: @desktop-width) {
  .contacts-info {
    margin-bottom: 50px;
  }

  .contacts-info__list {
    display: flex;
    justify-content: space-between;
    width: 960px;
  }

  .contacts-info__title {
    margin-bottom: 60px;

    &::before,
    &::after {
      content: "";
      flex-grow: 1;
      flex-shrink: 0;
      width: 281px;
      height: 1px;
      background-color: #e5e5e5;
    }

    &::before {
      margin-right: 60px;
    }

    &::after {
      margin-left: 60px;
    }
  }
}
